import { Github, ExternalLink, Video } from "lucide-react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent } from "@/components/ui/card";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import Image from "next/image";
import { AspectRatio } from "@/components/ui/aspect-ratio";
import { useState } from "react";

interface ProjectCardProps {
  project: {
    id: number;
    author: {
      name: string;
      avatar: string;
      username: string;
    };
    title: string;
    description: string;
    tags: string[];
    images: string[];
    video?: string;
    githubUrl: string;
    liveUrl: string;
  };
  variant?: "default" | "embedded";
}

export function ProjectCard({
  project,
  variant = "default",
}: ProjectCardProps) {
  const [selectedMedia, setSelectedMedia] = useState<string>(
    project.images[0] || project.video || ""
  );

  const outerCardClass =
    variant === "embedded"
      ? "bg-transparent border-none shadow-none rounded-none"
      : "bg-card border-border overflow-hidden";
  const innerPadding = variant === "embedded" ? "p-4 md:p-5" : "p-5";
  const mediaPadding = variant === "embedded" ? "p-4 md:p-5" : "p-5";
  const tagsBorder =
    variant === "embedded"
      ? "border-t border-border"
      : "border-t border-border";

  return (
    <Card className={outerCardClass}>
      <CardContent className="p-0">
        <div className="flex flex-col md:flex-row md:items-start">
          {/* Left Section - Text Content */}
          <div className={`flex-1 ${innerPadding}`}>
            {/* Author Info */}
            <div className="flex items-center gap-3 mb-4">
              <Avatar className="h-10 w-10">
                <AvatarImage
                  src={project.author.avatar || "/placeholder.svg"}
                />
                <AvatarFallback>{project.author.name[0]}</AvatarFallback>
              </Avatar>
              <div>
                <p className="font-medium text-foreground">
                  {project.author.name}
                </p>
                <p className="text-sm text-muted-foreground">
                  {project.author.username}
                </p>
              </div>
            </div>

            {/* Title */}
            <h3 className="text-xl font-semibold text-foreground mb-3">
              {project.title}
            </h3>

            {/* Description */}
            <p className="text-muted-foreground text-sm leading-relaxed mb-4">
              {project.description}
            </p>

            {/* Action Buttons */}
            <div className="flex items-center gap-3">
              <Button
                variant="outline"
                size="sm"
                className="gap-2 border-border hover:bg-muted hover:text-gray-400 bg-transparent "
                asChild
              >
                <a
                  href={project.githubUrl}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <Github className="h-4 w-4" />
                  GitHub
                </a>
              </Button>
              <Button
                variant="outline"
                size="sm"
                className="gap-2 border-border hover:bg-muted hover:text-gray-400 bg-transparent"
                asChild
              >
                <a
                  href={project.liveUrl}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <ExternalLink className="h-4 w-4" />
                  Live Link
                </a>
              </Button>
            </div>
          </div>

          {/* Right Section - Media (Amazon-style) */}
          <div
            className={`w-full md:max-w-[560px] ${mediaPadding} grid grid-cols-[64px_1fr] gap-3 min-w-0`}
          >
            {/* Vertical Thumbnails */}
            <div className="flex flex-col gap-2">
              {project.images.slice(0, 3).map((img, index) => (
                <button
                  key={index}
                  className={`w-16 h-16 rounded-md overflow-hidden border cursor-pointer ${
                    selectedMedia === img
                      ? "border-foreground"
                      : "border-border"
                  }`}
                  onClick={() => setSelectedMedia(img)}
                >
                  <Image
                    src={img}
                    alt={`Thumbnail ${index + 1}`}
                    className="w-full h-full object-cover"
                    width={64}
                    height={64}
                  />
                </button>
              ))}
              {project.images.length > 3 && (
                <Dialog>
                  <DialogTrigger asChild>
                    <button className="w-16 h-16 rounded-md overflow-hidden border border-border bg-muted/40 text-xs font-semibold">
                      +{project.images.length - 3}
                    </button>
                  </DialogTrigger>
                  <DialogContent className="sm:max-w-2xl">
                    <DialogHeader>
                      <DialogTitle>Gallery</DialogTitle>
                    </DialogHeader>
                    <div className="grid grid-cols-3 gap-3">
                      {project.images.map((img, i) => (
                        <button
                          key={i}
                          className={`aspect-square rounded-md overflow-hidden border ${
                            selectedMedia === img
                              ? "border-foreground"
                              : "border-border"
                          }`}
                          onClick={() => setSelectedMedia(img)}
                        >
                          <Image
                            src={img}
                            alt={`Image ${i + 1}`}
                            className="w-full h-full object-cover"
                            width={240}
                            height={240}
                          />
                        </button>
                      ))}
                      {project.video && (
                        <button
                          className={`aspect-square rounded-md overflow-hidden border flex items-center justify-center ${
                            selectedMedia === project.video
                              ? "border-foreground"
                              : "border-border"
                          }`}
                          onClick={() => setSelectedMedia(project.video!)}
                        >
                          <Video className="h-8 w-8 text-muted-foreground" />
                        </button>
                      )}
                    </div>
                  </DialogContent>
                </Dialog>
              )}
              {project.video && (
                <button
                  className={`w-16 h-16 rounded-md overflow-hidden border cursor-pointer flex items-center justify-center ${
                    selectedMedia === project.video
                      ? "border-foreground"
                      : "border-border"
                  }`}
                  onClick={() => setSelectedMedia(project.video!)}
                >
                  <Video className="h-6 w-6 text-muted-foreground" />
                </button>
              )}
            </div>

            {/* Large Media Block */}
            <div className="flex-1 rounded-md overflow-hidden bg-muted border border-border">
              {selectedMedia.endsWith(".mp4") ||
              selectedMedia === project.video ? (
                <AspectRatio ratio={16 / 9}>
                  <video
                    src={selectedMedia}
                    controls
                    className="w-full h-full object-cover"
                  />
                </AspectRatio>
              ) : (
                <AspectRatio ratio={16 / 9}>
                  <Image
                    src={selectedMedia}
                    alt="Selected media"
                    className="w-full h-full object-cover"
                    width={640}
                    height={360}
                  />
                </AspectRatio>
              )}
            </div>
          </div>
        </div>

        {/* Bottom Section - Tags */}
        <div className={`p-5 ${tagsBorder} bg-muted/30`}>
          <p className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-2">
            Tags
          </p>
          <div className="flex flex-wrap gap-2">
            {project.tags.map((tag) => (
              <Badge
                key={tag}
                variant="secondary"
                className="bg-secondary text-secondary-foreground text-xs"
              >
                {tag}
              </Badge>
            ))}
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
