"use client"

import { useState } from "react"
import { User, Palette, FolderKanban, LogOut, X, Sun, Moon, Monitor, ChevronDown, ChevronUp } from "lucide-react"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Button } from "@/components/ui/button"
import Link from "next/link"
import { useTheme } from "@/components/theme-provider"
import { useSession, signOut } from "next-auth/react"

interface SidebarProps {
  isOpen: boolean
  onClose: () => void
}

export const menuItems = [
  { name: "My Profile", href: "/profile", icon: User },
  { name: "Project Collection", href: "/collections", icon: FolderKanban },
]

export const themeOptions = [
  { name: "Light", value: "light" as const, icon: Sun },
  { name: "Dark", value: "dark" as const, icon: Moon },
  { name: "System", value: "system" as const, icon: Monitor },
]

export function Sidebar({ isOpen, onClose }: SidebarProps) {
  const [isThemeOpen, setIsThemeOpen] = useState(false)
  const { theme, setTheme } = useTheme()
  const { data: session } = useSession()

  return (
    <>
      {/* Overlay */}
      {isOpen && <div className="fixed inset-0 bg-background/80 backdrop-blur-sm z-40" onClick={onClose} />}

      {/* Sidebar */}
      <aside
        className={`fixed top-0 left-0 z-50 h-full w-72 bg-sidebar border-r border-border transform transition-transform duration-300 ease-in-out ${
          isOpen ? "translate-x-0" : "-translate-x-full"
        }`}
      >
        <div className="flex flex-col h-full">
          {/* Header */}
          <div className="flex items-center justify-between p-4 border-b border-border">
            <span className="text-lg font-semibold">Menu</span>
            <Button variant="ghost" size="icon" className="rounded-full w-9 h-9 hover:bg-muted" onClick={onClose}>
              <X className="h-5 w-5" />
            </Button>
          </div>

          {/* User Profile Section */}
          <div className="p-4 border-b border-border">
            <div className="flex items-center gap-3">
              <Avatar className="h-12 w-12">
                <AvatarImage src={session?.user?.image || "/placeholder-user.jpg"} />
                <AvatarFallback>U</AvatarFallback>
              </Avatar>
              <div>
                <p className="font-medium">{session?.user?.name || "User"}</p>
                <p className="text-sm text-muted-foreground">{session?.user?.email || ""}</p>
              </div>
            </div>
          </div>

          {/* Menu Items */}
          <nav className="flex-1 p-4">
            <ul className="space-y-1">
              {menuItems.map((item) => (
                <li key={item.name}>
                  <Link
                    href={item.href}
                    className="flex items-center gap-3 px-3 py-2.5 rounded-lg text-muted-foreground hover:text-foreground hover:bg-muted transition-colors"
                    onClick={onClose}
                  >
                    <item.icon className="h-5 w-5" />
                    <span>{item.name}</span>
                  </Link>
                </li>
              ))}

              {/* Theme dropdown menu */}
              <li>
                <button
                  onClick={() => setIsThemeOpen(!isThemeOpen)}
                  className="w-full flex items-center justify-between gap-3 px-3 py-2.5 rounded-lg text-muted-foreground hover:text-foreground hover:bg-muted transition-colors"
                >
                  <div className="flex items-center gap-3">
                    <Palette className="h-5 w-5" />
                    <span>Theme</span>
                  </div>
                  {isThemeOpen ? <ChevronUp className="h-4 w-4" /> : <ChevronDown className="h-4 w-4" />}
                </button>

                {/* Theme Options Submenu */}
                {isThemeOpen && (
                  <ul className="ml-4 mt-1 space-y-1 border-l border-border pl-4">
                    {themeOptions.map((option) => (
                      <li key={option.value}>
                        <button
                          onClick={() => setTheme(option.value)}
                          className={`w-full flex items-center gap-3 px-3 py-2 rounded-lg transition-colors ${
                            theme === option.value
                              ? "bg-primary text-primary-foreground"
                              : "text-muted-foreground hover:text-foreground hover:bg-muted"
                          }`}
                        >
                          <option.icon className="h-4 w-4" />
                          <span className="text-sm">{option.name}</span>
                        </button>
                      </li>
                    ))}
                  </ul>
                )}
              </li>
            </ul>
          </nav>

          {/* Logout */}
          <div className="p-4 border-t border-border">
            <Button
              variant="ghost"
              className="w-full justify-start gap-3 text-muted-foreground hover:text-foreground hover:bg-muted"
              onClick={() => signOut({ callbackUrl: "/login" })}
            >
              <LogOut className="h-5 w-5" />
              <span>Log out</span>
            </Button>
          </div>
        </div>
      </aside>
    </>
  )
}
